    mounted:function(){
      this.cityList = [{id:0, confirm:''}];
      this.districtList = [{id:0,confirm:''}];

    },
    methods:{
      newaddressBack:function () {
        this.$router.go(-1);
      },
      save:function () {
        var _this = this;
        var userconfirm = $('#userconfirm').val();
        var userphone = $('#userphone').val();
        var useraddress = $('#useraddress').val();
        var province = $('#province>option:selected').text();
        var city = $('#city>option:selected').text();
        var district = $('#district>option:selected').text();
        var address = $("#useraddress").val();
        var userId = getCookie("userId");
        var p_id = _this.p_id;
        var c_id = _this.c_id;
        var d_id = _this.d_id;
        console.log(district)
        if(userconfirm === '' || userphone ===''|| useraddress === '' || province === '' || city === '' || district === ''){
          _this.$layer.msg("");
        }else{
          $.ajax({
            type: 'POST',
            url: urlhost+"useraddress/address",
            data$Type: "jsonp",
            data: {
              'user_id': userId,
              'province':province,
              'city':city,
              'district':district,
              'address':address,
              'consignee':userconfirm,
              'mobile':userphone,
            },
            success: function (response) {
              var mess = JSON.parse(response);
              console.log(district)
              if(mess.status === 0){
                _this.$layer.msg(mess.message)
              }else{
                _this.$layer.msg(mess.message);
                _this.$router.push({
                  name:"address",
                  params:{
                    'p_id':p_id,
                    'c_id':c_id,
                    'd_id':d_id
                  }
                })
              }
            },
          });
        }
      },
      _filter(add,confirm,code) {
        let result = [];
        for(let i=0;i<add.length;i++) {
          if(code == add[i].id){
            result = add[i][confirm];
          }
        }
        return result;
      },

      //
      provinceSelected: function() {

        //
        this.province = $('#province>option:selected').text();
        this.city = $('#city>option:selected').text();

        //
        let i = $('#province>option:selected').val();

        this.cityList = this.info[i].city;

        //1onchange
        if(this.cityList.length == 1){
          this.districtList = this.cityList[0].district;
        }else{
          this.districtList = this.cityList[i].district;
        }

        //id
        this.p_id = this.info[i].id;
        this.c_id = this.cityList[0].id;
        this.d_id = this.districtList[0].id;
      },

      //
      citySelected: function() {

        //
        this.city = $('#city>option:selected').text();
        this.district = $('#district>option:selected').text();

        //district
        let i = $('#city>option:selected').val();

        this.c_id = this.cityList[i].id;
        this.districtList = this.cityList[i].district;
        this.d_id = this.districtList[0].id;
      },

      //
      districtSelected: function() {

        //
        this.district = $('#district>option:selected').text();

        //district
        let i = $('#district>option:selected').val();
        this.d_id = this.districtList[i].id;
      }
    }
