        width: 100%;
        background: #ffffff;
        .consignee{
          display: inline-flex;
          justify-content: space-between;
          width: 100%;
          padding-bottom: 8px;
        }
        .shipping{
          padding-bottom: 8px;
          word-break: break-all;
          word-wrap: break-word;
          border-bottom: 1px solid #f2f2f2;
        }
      }
      .defaultAddress{
        width: 100%;
        height: n(60);
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
        .modify{
          img{
            width: n(40);
            height: n(40);
          }
        }
      }
    }
    .add{
      position: absolute;
      top:80%;
      width: 100%;
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      img{
        width: n(80);
        height: n(80);
      }
    }
  }

